.sly-link-button {
  color: indigo;
  background-color: rgba(89, 2, 151, 0);
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.4);
  position: relative;
  display: inline-block;
  padding: .35rem;
  border: 1px solid transparent;
  border-radius: 3px;
  outline: none;
  font-family: inherit;
  font-size: 1.2rem;
  font-weight: normal;
  line-height: 1.5rem;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}
.sly-link-button:hover {
  padding: .35rem;
  border: 1px solid transparent;
  border-radius: 3px;
  background-color: rgba(72, 2, 121, 0.123);
  outline: none;
  cursor: pointer;
  user-select: none;
  transition: background-color .1s ease-in-out;
}

.sly-link-button-container {
  margin: 4px;
  display: flex;
  flex-direction: column;
}
